import React, { Component } from 'react'
import update from 'immutability-helper'
import Game from './game/Game.js'
import './App.css'

export default class App extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }

getScore = (n) => {
  this.setState({ score : n })
}

  render() {
    return (
      <span id='view'>
        <Input />
        <Game fill='#eeeeee' score={this.getScore} />
        <Score score={this.state.score} />
      </span>
    )
  }
}

class Input extends Component {
  render() {
    return (
      <span class='unselect' style={styles.input}/>
    )
  }
}

class Score extends Component {
  constructor(props) {
    super(props)
    this.state = {
        score: 0,
    }
  }

  render() {
    return (
      <span id='score' class='unselect' style={styles.score}>
        {this.props.score}
      </span>
    )
  }
}

const styles = {
  score: {
    fontSize: '6vh',
    color: '#eee',
    textAlign: 'center',
    zIndex: 3,
    position: 'absolute',
  },
  input: {
    width: '100%',
    height: '100%',
    position: 'absolute',
    backgroundColor: 'cyan',
    opacity: 0.3,
    zIndex: 5,
  },
}
